* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #3b7577;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.2em;
  line-height: 1.5;
}

header {
  background-color: #131d1d;
}

li {
  list-style: none;
}

a {
  color: #fff;
  text-decoration: none;
}

.dropdown-menu {
  justify-content: space-between;
  align-items: center;
  min-height: 90px;
  padding: 0 24px;
  display: flex;
}

.logo {
  font-size: 2rem;
}

.menu-link {
  transition: all .7s;
}

.menu-link:hover {
  color: #1e90ff;
}

.hamburger-menu {
  display: block;
}

.hamburger-bar {
  background-color: #fff;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  transition: all .3s ease-in-out;
  display: block;
}

.hamburger-menu.active .hamburger-bar:nth-child(2) {
  opacity: 0;
}

.hamburger-menu.active .hamburger-bar:first-child {
  transform: translateY(8px)rotate(45deg);
}

.hamburger-menu.active .hamburger-bar:nth-child(3) {
  transform: translateY(-8px)rotate(-45deg);
}

.menu-content {
  text-align: center;
  background-color: #131d1d;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 60px;
  width: 100%;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 70px;
  left: -100%;
}

.menu-item {
  margin: 16px 0;
}

.menu-content.active {
  left: 0;
}

main {
  background-color: #faf4ec;
  width: 400px;
  margin: 2em auto;
  padding: 3em;
}

main p {
  margin-bottom: 1.6em;
  padding: 0;
}

main br {
  margin-bottom: 1.2em;
}

picture img {
  margin-bottom: 1.5em;
}

ol li {
  margin: 1.1em;
  list-style-type: upper-roman;
}

@media (width >= 600px) {
  main {
    width: 600px;
    margin: 2.7em auto auto;
  }

  .main-img {
    text-align: center;
    align-items: center;
  }

  picture img {
    margin: -.5em 1em 1.6em;
  }
}

@media (width >= 1024px) {
  main {
    width: 800px;
    margin: 2.7em auto auto;
  }

  picture img {
    margin: -.5em 1.4em 1.4em;
  }
}
